body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size:16px;
  font-family: 'Roboto', sans-serif;
  font-weight: 200;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
form h5{
  padding:14px 0;
}
.collapsible-body span{
  display: block;
}
.collapsible-body div.btn{
  margin-top:10px;
}
li.collection-item{
  padding:120px 0;
}
.card-title{
  padding:10px;
  /* font-family: Avenir; */
  margin-bottom:-10px;
}

p.text-title{
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
  margin-right:50px;
  color:black;
}
span.secondary-content{
 margin-right:20px;
 position: absolute;
 right:0px;
 top:0px;
}
p{
  margin:0;
  padding:0;
  word-wrap: break-word;
}
.card-action div{
  display: inline-block;
  margin-left:10px;
}
button.btn{
  margin-top:-15px;
  margin-bottom:-10px;
}
div.collection-item div{
  margin-top:10px;
  margin-right:10px;
  display:inline-block;
}
div.collection-item p {
  margin-top:6px;
}
div.input-field{
  margin-top:-4px;
}
div.topic-list div.card {
  margin-top:-14px;
}
div.card div.card-content p{
  margin-top:10px;
}
.material-icons{
  font-size:24px;
}
.material-icons:hover{
  color:red;
  cursor: pointer;
}
.delete-comment{
  float:right;
}
.brand-logo span{
  font-size:20px;
  font-weight: 900;
  text-transform: uppercase;
}
ul.sidenav div li .red{
  color: white;
}
